@import "../sass-utils/mixnis";
@import "../sass-utils/vars";





.inquirer-container {
	padding-top: 40px;
	position: relative;
	z-index: 10;
	p {
		margin-bottom: 20px;
		font-size: 25px;
		display: inline-block;
		text-shadow: 0px -1px 1px #feff37, 0px 1px 1px #feff37; }
	@media (max-width: 767px) {
		.title-text {
			font-size: 30px; } }
	@media (max-width: 550px) {
		.btn-box-inquirer {
			flex-wrap: wrap;
			flex-direction: column;
			align-items: flex-start;
			justify-content: space-between;
			.btn {
				margin-bottom: 0; } }
		.btn-dang-Inquirer {
			margin-left: auto; }
		.styles_modal__gNwvD {
			transform: skew(0deg, 0deg); }
		.ask-one-curve {
			transform: skew(2deg, 2deg);
			.ask-one {
				transform: skew(-2deg, -2deg); } } }
	@media (max-width: 400px) {
		.styles_overlay__CLSq- {
			padding: 0; }
		p {
			font-size: 18px; }
		.ask-proviso,label,.form-control {
			font-size: 16px; }
		.ul-label {
			margin-left: 0; } } }

.img-inquirer-title {
	margin-top: -138px;
	margin-bottom: -80px;
	position: relative;
	z-index: 10;
	@media (max-width: 1200px) {
		margin-left: -27px; }
	@media (max-width: 991px) {
		margin-left: 0; } }

.ul-label {
	margin-left: 18px; }

.ask-proviso {
	font-size: 20px;
	margin-bottom: 15px; }

[class$=curve],
.styles_modal__gNwvD {
   padding: 40px;
   border: 2px solid rgba(142, 142, 147, .9);
   border-radius: 8px;
   margin: 20px auto;
   background: url("../../../img/bg-ask.jpg") no-repeat 50%/cover;
   box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22), inset 2px 2px 5px rgba(154, 147, 140, 0.5), 1px 1px 5px rgba(255, 255, 255, 1);
   @media (max-width: 550px) {
   		padding: 25px; } }

.styles_modal__gNwvD {
   @media (max-width: 550px) {
   		padding-top: 45px; } }



.ask-one-curve {
	transform: skew(22deg, 2deg); }

.ask-one {
	transform: skew(-22deg, -2deg); }

.ask-two-curve {
	transform: skew(2deg, 2deg); }

label,.form-control {
	font-size: 18px;
	letter-spacing: .5px; }

label {
	text-shadow: 0px -1px 1px #feff37, 0px 1px 1px #feff37; }

input {
	&:focus::-webkit-input-placeholder {
		opacity: 0;
		transition: opacity .3s ease; }
	&:focus::-moz-placeholder {
		opacity: 0;
		transition: opacity .3s ease; }
	&:focus:-moz-placeholder {
		opacity: 0;
		transition: opacity .3s ease; }
	&:focus:-ms-input-placeholder {
		opacity: 0;
		transition: opacity .3s ease; } }

input[type="radio"],
input[type="checkbox"] {
	display: none;
	&:checked+label:before {
		background: linear-gradient(to bottom, #145b32 0%, #fa9baf 100%);
		box-shadow: inset 0px 1px 2px white, 0px 1px 3px rgba(0, 0, 0, 0.5); } }

input[type="radio"]+label,
input[type="checkbox"]+label {
	padding: 5px 5px 5px 30px;
	position: relative;
	margin-bottom: 0;
	cursor: pointer;
	&:before {
		content: "";
		position: absolute;
		top: 50%;
		border-radius: 50%;
		transform: translateY(-50%);
		left: 0;
		width: 18px;
		height: 18px;
		border: 2px solid #fff;
		box-shadow: inset 0px 11px 10px white, 0px 1px 3px rgba(0, 0, 0, 0.6);
		background: grey; } }

input[type="checkbox"]+label {
	&:before {
		border-radius: 0; } }

.ask-three-curve,
.ask-four-curve {
	transform: skew(-1deg, 2deg); }


.ask-three,
.ask-four {
	transform: skew(-1deg, 0deg);
	padding: 0px 1px 2px 5px; }


.btn-box-inquirer {
	display: flex;
	justify-content: center;
	position: relative;
	margin: 20px 0 40px;
	perspective: 300px {
   	transform-style: preserve-3d; }
	.img-inquirer {
		position: absolute;
		bottom: 0;
		right: 0;
		margin: 0;
		@media (max-width: 991px) {
			bottom: 63%; } } }

.btn {
   position: relative;
   overflow: hidden;
   margin: 20px 15px;
   letter-spacing: .5px;
   font-size: 25px;
   border-radius: 40px;
   transition: all .5s ease;
   box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22), inset 2px 2px 5px rgba(154,147,140,.5), 1px 1px 5px #fff;
   &:focus {
   	  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22), inset 2px 2px 5px rgba(154,147,140,.5), 1px 1px 5px #fff; }
   @media(max-width: 550px) {
      margin-top: 8px;
      padding: 8px 20px 7px 20px;
      margin-bottom: 10px;
      font-size: 20px; } }


.btn-warn {
	padding: 13px 24px;
	border-radius: 28px;
	background: #feff37;
	transform: skewX(-7deg);
	&:hover {
		background: #feff37 * 1.9; }
	&:active {
	  	background: rgba(254, 255, 55, .9);
	  	transform: skewX(-7deg) scale(.99);
	  	box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22), inset 2px 2px 5px rgba(154,147,140,.5), 1px 1px 5px #fff; }
	&:before {
      content: "";
      position: absolute;
      top: -10%;
      bottom: -10%;
      left: -10%;
      right: -10%;
      background: url("../../../img/bubbling.png") repeat 0 400px;
      animation: bubbling linear infinite 7s;
      @media (max-width: 767px) {
      	display: none; } } }

.btn-warn-Inquirer {
   border-top: 4px solid #ffc107;
   border-left: 2px solid #ffc107;
   border-right: 5px solid #ffc107;
   border-bottom: 6px solid #ffc107;
   transform: rotateX(20deg) !important;
   &:active {
   	   transform: rotateX(20deg) scale(.95) !important; } }


.btn-warn-modal {
	padding: 10px 20px;
	border: 2px solid #ffc107; }


.btn-warn-modal:hover {
   	box-shadow: 0 0 0 0.2rem rgba(255, 193, 7, .5); }

.btn-dang {
	padding: 13px 24px;
	border-radius: 28px;
	background: #dc3545;
	transform: skewX(5deg);
	color: #fff;
	&:hover {
		background: #dc3545 * 1.3;
		color: #fff; }
	&:active {
	  	background: #dc3545 * 1.3;
	  	transform: skewX(5deg) scale(.95);
	  	color: #fff;
	  	box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22), inset 2px 2px 5px rgba(154,147,140,.5), 1px 1px 5px #fff; }
	&:before {
      content: "";
      position: absolute;
      top: -10%;
      bottom: -10%;
      left: -10%;
      right: -10%;
      background: url("../../../img/bubbling.png") repeat 0 400px;
      animation: bubbling linear infinite 5s;
      @media (max-width: 768px) {
      	display: none; } } }


.btn-dang-Inquirer {
   border-top: 4px solid #b21f2d;
   border-left: 5px solid #b21f2d;
   border-right: 3px solid #b21f2d;
   border-bottom: 6px solid #b21f2d;
   transform: rotateX(20deg) !important;
   &:active {
   	   transform: rotateX(20deg) scale(.95) !important; } }


@keyframes bubbling {
    from {
      background-position: 0  0; }

    to {
      background-position: 0 -400px; } }

.btn-dang-modal {
	padding: 10px 20px;
	border: 2px solid #b21f2d; }


.btn-dang-modal:hover {
   	box-shadow: 0 0 0 0.2rem rgba(178, 31, 45, .5); }

.styles_overlay__CLSq- {
	background: rgba(0, 188, 212, .5);
	backdrop-filter: blur(10px);
	padding: 40px 5px 20px; }


.styles_modal__gNwvD {
	transform: skew(2deg, 2deg);
	h2 {
		text-shadow: 0px -1px 1px #feff37, 0px 1px 1px #feff37; }
	@media (max-width: 550px) {
		transform: skew(0deg, 0deg);
		h2 {
			font-size: 24px; } } }

.img-inquirer {
	margin-right: 16%;
	transform: translateY(-16%);
	@media (max-width: 991px) {
		transform: translateY(0);
		margin-right: 0; } }


.img-modal {
	float: right; }

.btn-box-modal {
	display: flex;
	margin-top: 40px; }

.styles_closeButton__20ID4 {
	&:hover, &:focus {
		outline: none; } }

.styles_closeIcon__1QwbI {
	width: 30px;
	height: 30px;
	transition: transform ease .3s;
	&:hover, &:focus {
		transform: scale(1.13); } }



